<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col md="12">
        <b-card>
          <div slot="header">
            Cari <strong>JUKIR</strong>
          </div>
          <b-form>
            <b-form-group validated
                          label="ID Jukir"
                          label-for="idJukir"
                          description="Silahkan masukkan id jukir">
              <b-form-input v-model="idJukir" id="idJukir" type="text" placeholder="" required/>
            </b-form-group>
            <div slot="footer">
              <b-button size="sm" variant="success" @click="searchJukirByQr()"><i class="fa fa-qrcode"></i> Cari QR</b-button>&nbsp;
              <b-button size="sm" variant="primary" @click="searchJukirByString()"><i class="fa fa-search"></i> Cari Id</b-button>&nbsp;&nbsp;
              <!--<b-button type="reset" size="sm" variant="danger"><i class="fa fa-ban"></i> Reset</b-button>-->
            </div>
          </b-form>

          <br>
          <vue-good-table
            :columns="columns"
            :rows="data"
            :pagination-options="{
    enabled: true,
    mode: 'pages',
    position: 'bottom',
    dropdownAllowAll: true,}"
            :search-options="{
    enabled: true,
    placeholder: 'Cari Jukir ',}">

            <template slot="table-row" slot-scope="props">
        <span v-if="props.column.field == 'action'">
          <b-button block variant="primary" @click="infoClicked(props.row)">Info</b-button>
          <b-button block variant="success" @click="updateClicked(props.row)">Update</b-button>
        </span>
              <span v-else-if="props.column.field == 'link_foto'">
          <img :src="'http://' + props.formattedRow[props.column.field]" class="thumbailTablePhoto"/>
        </span>
              <span v-else>
        {{props.formattedRow[props.column.field]}}
        </span>
            </template>
          </vue-good-table>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import {EventBus} from '../../_auth/utils/eventbus'

  export default {
    name: 'cari-jukir',

    data() {
      return {
        idJukir :"",
        errorMsg: '',
        columns: [
          {
            label: 'ID Jukir',
            field: 'id',
            thClass: 'text-center',
          },
          {
            label: 'Foto',
            field: 'link_foto',
            html: 'true',
            tdClass: 'text-center',
            thClass: 'text-center',
            width: '70px'
          },
          // {
          //   label: 'ID/Nama',
          //   field: 'id_and_name',
          //   thClass: 'text-center',
          // },
          {
            label: 'Nama',
            field: 'nm',
            thClass: 'text-center',
          },
          {
            label: 'Jalan',
            field: 'jalan',
            thClass: 'text-center',
          },
          {
            label: 'Action',
            field: 'action',
            thClass: 'text-center',
          },
        ],
        data: [],
        selected_jukir1: [],
        errorMsg: '',
      }
    },

    methods: {
      searchJukirByQr()
      {
        // let str_id = "0000001001%230000000610%23K.01.14.0610"
        // let n = str_id.indexOf("%%23");
        // console.log(n);
        // this.$router.push({
        //   path: '/info/info-jukir',
        //   query: {id: "0000001001#0000000610#K.01.14.0610"},
        // });
        this.$router.push({
          path: '/cari-qr-jukir',
        });
      },

      searchJukirByString() {
        this.$store.dispatch('find_info_jukir', {id: this.idJukir})
          .then(() => this.$router.push('cari-jukir'))
      },

      updateClicked(param) {
        this.$router.push({
          path: '/detail/detail-jukir',
          query: {id: param.id}
        });
      },
      infoClicked(param) {
        this.$router.push({
          path: '/info/info-jukir',
          query: {id: param.id}
        });
      },
    },

    mounted() {
      EventBus.$on('success', (msg) => {
        this.data = msg;
        this.errorMsg = "";
      });
      EventBus.$on('fail', (msg) => {
        this.data = [];
        this.errorMsg = msg;
      })
    },

    beforeDestroy() {
      EventBus.$off('success');
      EventBus.$off('fail');
    },
  };
</script>
